 /* Front page */
 /*Banner*/
 .front-page-hero {
     background-image: linear-gradient(180deg, rgba(0, 0, 0, .4) 0, rgba(0, 0, 0, .4)), url(../../images/home-hero.jpg);
     background-size: cover;
     height: 100vh;
     position: relative;
     z-index: 2;
     .hero-banner-logo {
         img {
             position: absolute;
             padding-left: 30.8rem;
             padding-top: 10.5rem; //  margin-left:30rem;
             // margin-top: 12rem; 
             display: block;
             margin: auto;
             justify-content: center;
         }
     }
 }
 /*front page sections */
 .front-section-container {
     margin-left: auto;
     margin-right: auto;
     width: 1120px;
