 /*Shop Page*/
 h1.page-title {
     text-transform: uppercase;
     text-align: center;
 }
 .archive .entry-content p {
     display: none;
     font-size: 0.00000001px;
     z-index: 5;
     margin: 0;
 }
 .taxonomy-description p {
     font-size: 1rem;
     text-align: center;
     color: $color__text-main; //   line-height: 1.75;
 } // .product-type-wrap{
 // }
 .product-types-container {
     display: flex;
     align-content: center;
     justify-content: space-around;
     width: 350px;
     margin-left: 24rem;
 }
 .text-uppercase {
     //  display:inline-flex;
     text-transform: uppercase;
     text-align: center;
     text-decoration: none;
     font-size: 1.2rem;
     margin-left: 0;
     padding-left: 0; // justify-content: space-between;
     font-family: $font_accent;
 }
 .page-header {
     margin-bottom: 40px;
     padding-bottom: 40px;
     border-bottom: $dashed_border;
 }
 .product-display {
     display: grid;
     grid-template-columns: repeat(4, 1fr);
     grid-row-gap: 25px;
     grid-column-gap: 30px;
     margin-bottom: 3rem;
     .product-display-item {
         height: 300px;
         border: $solid_border;
         overflow: hidden;
         img {
             // display:none;
             height: 250px;
             object-fit: cover;
             border-bottom: 5px solid #e1e1e1;
             padding-bottom: 0;
             margin-bottom: 0;
         }
     }
     .product-display-item .entry-title a {
         display: none;
         text-align: center;
         font-size: 0.00000001px;
         z-index: 1;
         margin: 0;
     }
 }
 .price {
     z-index: 999;
     color: black;
     font-size: 1rem; // display: inline-flex;
     // padding-top: 0;
     // margin-top: 0;
     text-align: center;
 }
 .archive .entry-content,
 .archive .hentry,
 .archive .entry-title {
     margin: 0;
 }

